.pagination {
    display: flex;
     /*padding-left: 0;*/
    justify-content: center;
    list-style: none;
    border-radius: 0.25rem;
    cursor: unset;
}
.page-item.disabled .page-link {
    color: #6c757d;
    pointer-events: none;
    cursor: not-allowed;
    background-color: #fff;
    border-color: #dee2e6;
}

.page-item.disabled {
    cursor: not-allowed ;
}
 th {
    cursor: default;
}

tbody:nth-child(even) {background: #ecebeb !important;}
tbody:nth-child(odd) {background: #FFF}
